.pl {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; 
}

.pl-texts{
  width: 65%;
}


.pl-desc{
  margin: 20px 0px;
  font-size: 20px;
}

.pl-list{
  width:100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media screen and (max-width: 480px){
  .pl{
    padding: 10px 10px;
    height: 65vh;
    margin-top: 250px;
  }

  .pl-texts{
    width: 100%;
  }

  .pl-desc{
    display: none;
  }
}